
.description {
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: row;
    gap: 10px;
}

@media  (max-width: 860px) {
    .description {
        flex-direction: column;
        height: auto;
    }
}

.dc-left {
    width: 50%;
    height: auto;
}

.dc-right {
    border-left: solid 1px #ccc;
    width: 50%;
    height: auto;
    overflow-y: auto;
    display: flex;
    flex-direction: column;
    align-items: center;

}

.scrollable-div::-webkit-scrollbar {
    display: none; /* 스크롤바 숨기기 */
  }